[data-theme='dark'] main {
  background-color: var(--dark-background-color);
  transition: 0.3s;
}

[data-theme='dark'] .gallery-title {
  color: var(--light-background-color);
  transition: 0.3s;
}

[data-theme='light'] main {
  transition: 0.3s;
}

[data-theme='dark'] .gallery-title {
  color: var(--light-background-color);
  transition: 0.3s;
}

[data-theme='dark'] .categories-item {
  color: var(--button-accent-text-color);
  transition: 0.3s;
}

[data-theme='dark'] .active {
  color: var(--dark-category-accent-color);
  transition: 0.3s;
}

[data-theme='dark'] .active {
  color: var(--dark-category-accent-color);
  transition: 0.3s;
}

[data-theme='dark'] .top-books-category-item-title {
  color: var(--button-accent-text-color);
  transition: 0.3s;
}

[data-theme='dark'] .top-books-category-btn {
  color: var(--button-accent-text-color);
  transition: 0.3s;
}
/* [data-theme='dark'] .top-books-category-btn:is(:hover, :focus){
      background-color: var(--dark-category-accent-color);
    } */

[data-theme='dark'] .modal {
  background-color: var(--dark-background-color);
  transition: 0.3s;
}



[data-theme='dark'] .book-name {
  color: var(--button-accent-text-color);
  transition: 0.3s;
}

[data-theme='dark'] .book-description {
  color: var(--button-accent-text-color);
  transition: 0.3s;
}

[data-theme='dark'] .logo-text {
  color: #f3f3f3;
  transition: 0.3s;
}

[data-theme='dark'] .header {
  background-color: var(--text-color);
  transition: 0.3s;
}

[data-theme='dark'] .item-text {
  color: var(--button-accent-text-color);
  transition: 0.3s;
}

[data-theme='dark'] .shpng-list-logo {
  fill: var(--button-accent-text-color);
  transition: 0.3s;
}

[data-theme='dark'] .footer {
  background-color: var(--dark-background-color);
  transition: 0.3s;
}

[data-theme='dark'] .footer-copyright {
  color: var(--button-accent-text-color);
  transition: 0.3s;
}

[data-theme='dark'] ::-webkit-scrollbar-thumb {
  background: var(--text-color);
  transition: 0.3s;
}
::-webkit-scrollbar-thumb {
  background: #e0e0e0;
  transition: 0.3s;
}

[data-theme='dark'] .add-list-btn {
  background-color: var(--dark-background-color);
  color: var(--button-accent-text-color);
  transition: 0.3s;
}

[data-theme='dark'] .add-list-btn:hover,
[data-theme='dark'] .add-list-btn:focus {
  background-color: var(--light-category-accent-color);
  color: var(--button-accent-text-color);
  transition: 0.3s;
}

[data-theme='dark'] .add-list-btn:hover:not(:focus),
[data-theme='dark'] .add-list-btn:focus:not(:hover),
[data-theme='dark'] .add-list-btn:active,
[data-theme='dark'] .add-list-btn:active:focus {
  color: var(--button-accent-text-color);
  background-color: initial;
  transition: 0.3s;
}

[data-theme='dark'] .book-title,
[data-theme='dark'] .description-shopping-list
/* [data-theme='dark'] .tui-page-btn{ */{
color: var(--button-accent-text-color);
transition: 0.3s;
}


[data-theme='dark'] .tui-pagination .tui-page-btn{
  color: var(--button-accent-text-color);
  transition: 0.3s;
}

[data-theme='dark'] .tui-pagination .tui-ico-next,
[data-theme='dark'] .tui-pagination .tui-ico-last {
  color: #000000;
  transition: 0.3s;
}

[data-theme='dark'] .tui-pagination .tui-first,
[data-theme='dark'] .tui-pagination .tui-first + .tui-prev {
  background-color: var(--text-color);
  transition: 0.3s;
}

[data-theme='dark'] .tui-pagination .tui-next,
[data-theme='dark'] .tui-pagination .tui-last {
  background-color: var(--dark-category-accent-color);
  border: 1px solid var(--text-color);
  transition: 0.3s;
}

[data-theme='dark'] .tui-page-btn.tui-is-selected {
  background-color: var(--button-accent-text-color);
  color: var(--text-color);
  border: 2px solid var(--text-color);
  transition: 0.3s;
}

[data-theme='dark'] .tui-pagination .tui-first + .tui-prev:hover{
  background-color: var(--button-accent-text-color);
  transition: 0.3s;
}

[data-theme='dark'] .tui-pagination .tui-next + .tui-last,
.tui-pagination .tui-first + .tui-prev{
  border-left: 1px solid var(--text-color);
  transition: 0.3s;
}

[data-theme='dark'] .tui-page-btn.tui-is-selected.tui-last-child{
  border-right: 2px solid var(--text-color);
  transition: 0.3s;
}

