:root {
  --dark-category-accent-color: #eac645;
  --light-category-accent-color: #4f2ee8;
  --dark-background-color: #202024;
  --light-background-color: #f6f6f6;
  --text-color: #111;
  --light-lift-color: #e0e0e0;
  --titles-color: #b4afaf;
  --button-accent-text-color: #fff;
}

body {
  color: var(--text-color);
  background-color: #f3f3f3;
  font-family: DM Sans, sans-serif;
}

.main-wrapper {
  padding-top: 104px;
}

.container {
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

.title {
  font-size: 32px;
  font-weight: 700;
}

.accent-color {
  color: var(--light-category-accent-color);
}

.hidden {
  visibility: none;
  display: none;
}

.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.buttons {
  font-size: 18px;
  font-style: bold;
  letter-spacing: -1%;
  color: var(--text-color);
  border: 2px;
  border-radius: 40px;
  padding: 20px 0;
  font-family: inherit;
  line-height: 1.33333;
}

.buttons:hover, .buttons:focus {
  background-color: var(--light-category-accent-color);
  color: var(--button-accent-text-color);
  font-style: normal;
}

.header-buttons-light {
  letter-spacing: -4%;
  color: var(--text-color);
  border: 2px;
  border-radius: 36px;
  gap: 8px;
  padding: 8px 16px;
  font-family: inherit;
  font-size: 14px;
  font-style: normal;
  line-height: 1.28571;
  display: flex;
}

.header-buttons-light:hover, .header-buttons-light:focus {
  background-color: var(--dark-category-accent-color);
  font-style: bold;
}

.header-buttons-dark {
  letter-spacing: -4%;
  color: var(--button-accent-text-color);
  border: 2px;
  border-radius: 36px;
  gap: 8px;
  padding: 8px 16px;
  font-family: inherit;
  font-size: 14px;
  font-style: normal;
  line-height: 1.28571;
  display: flex;
}

.header-buttons-dark:hover, .header-buttons-dark:focus {
  background-color: var(--dark-category-accent-color);
  color: var(--text-color);
  font-style: bold;
}

