.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  width: 40px;
  height: 20px;
  transition: all .9s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider:before {
  content: "";
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  width: 19px;
  height: 19px;
  transition: all .3s;
  position: absolute;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

input:checked + .slider {
  background: linear-gradient(#4f2ee8 60%, #686868 100%);
}

input:checked + .slider:before {
  background-color: #000;
  transform: translateX(20px);
}

.slider.round {
  border-radius: 80px;
}

:before {
  border-radius: 50%;
}

.title-shopping-list {
