body.modal-team-open {
  overflow: hidden;
}

.backdrop-team {
  z-index: 999;
  opacity: 1;
  background-color: rgba(0, 0, 0, .8);
  width: 100%;
  height: 100%;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop-team--hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-team {
  background-color: #f6f6f6;
  border: 2px solid #111;
  border-radius: 18px;
  width: 80%;
  max-width: 900px;
  height: auto;
  padding: 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 20px;
  left: 50%;
